import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
export default {
name: "",
components: {
	uniRate
},
props: {},
data () {
  return {
	  trailerId: "",
	  trailerdetail: {},
	  director: [],
	  actor: [],
	  plotPics: []
    }
  },
  methods: {
	// 获取预告片详情
	getTrailerDetail() {
		uni.request({
			url: `${this.$api}/search/trailer/${this.trailerId}?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.trailerdetail = res.data.data
				this.plotPics = JSON.parse(res.data.data.plotPics)
				console.log(res,'预告片详情')
			},
			fail: () => {},
			complete: () => {}
		});
	},
	// 预览图片
	previewImg() {
		let arr = []
		arr.push(this.trailerdetail.cover)
		let that = this
		uni.previewImage({
		    urls: arr,
		    longPressActions: {
		        itemList: ['保存图片'],
		        success: function(data) {
					uni.saveImageToPhotosAlbum({
					    filePath: that.trailerdetail.cover,
					    success: function () {
							uni.showToast({
								title: '保存成功'
							})
							console.log('save success');
					    }
					});
		        },
		        fail: function(err) {
		            console.log(err.errMsg);
		        }
		    }
		});
	},
	// 获取导演
	getDirector() {
		uni.request({
			url: `${this.$api}/search/staff/${this.trailerId}/1?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.director = res.data.data
				console.log(res,'导演')
			},
			fail: () => {},
			complete: () => {}
		});
	},
	// 获取演员
	getActor() {
		uni.request({
			url: `${this.$api}/search/staff/${this.trailerId}/2?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.actor = res.data.data
				console.log(res,'演员')
			},
			fail: () => {},
			complete: () => {}
		});
	}
  },
  mounted () {

  },
  onLoad (options) {
	this.trailerId = options.id
	this.getTrailerDetail()
	this.getDirector()
	this.getActor()
  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
